.about {
    &_title {
      margin: 0 0 48px;
      font-weight: bold;
      font-size: 36px;
      line-height: 36px;
      align-items: center;
      letter-spacing: -1.5px;
      color: #000;
  
      @include forDesktop() {
        font-size: 48px;
        line-height: 48px;
        margin-bottom: 0;
      }
    }
  
    &_titleLink {
      color: $perfectBlue;
      text-decoration: none;
    }
  
    &_subtitle {
      display: block;
      margin-bottom: 16px;
      color: $perfectBlue;
      font-weight: bold;
      text-decoration: none;
      font-size: 24px;
      line-height: 30px;
    }
  
    &_paragraph {
      margin: 0 0 27px;
  
      &:last-child {
        margin-bottom: 0;
      }
  
      &Important {
        font-weight: bold;
      }
    }
  }