.page {
    font-family: 'Roboto', sans-serif;
    color: #3e3e3e;
    font-weight: normal;
    font-size: 16px;
    line-height: 27px;
    scroll-behavior: smooth;
  
    &_body {
      margin: 0;
    }
  
    &_menu {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      transform: translateX(-100%);
      transition: transform 0.5s;
  
      &:target {
        transform: translateX(0);
      }
    }
  
    &_section {
      margin-bottom: 64px;
  
      @include forDesktop() {
        margin-bottom: 104px;
      }
    }
  
    &_sectionTitle {
      margin: 0 0 32px;
      font-size: 36px;
      line-height: 36px;
      font-weight: bold;
      text-align: center;
      letter-spacing: -1.5px;
      color: #000;
  
      @include forDesktop() {
        margin-bottom: 56px;
        font-size: 48px;
        line-height: 48px;
      }
  
      &::after {
        display: block;
        background-position: center;
        margin: 4px auto 0;
        background-size: contain;
        background-repeat: no-repeat;
        width: 23px;
        height: 16px;
        content: "";
        background-image: url(../images/crown.svg);
      }
    }
  }