.header {
    background-image: url(../images/header.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  
    &_content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
  
      padding: $headerTopPadding 0 80px;
      box-sizing: border-box;
      height: 100vh;
    }
  
    &_call {
      text-decoration: none;
  
      @include forDesktop() {
        &::before {
          display: inline-block;
          padding-top: -50%;
          transform: translate(-100%, 50%);
          padding-right: 20px;
          content: "+1 234 555-55-55";
          color: #fff;
          font-size: 12px;
          line-height: 16px;
          font-weight: bold;
          text-transform: uppercase;
          letter-spacing: 2px;
          width: max-content;
          opacity: 0;
          pointer-events: none;
        }
  
        &:hover::before {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
  
    &_title {
      font-weight: bold;
      font-size: 46px;
      line-height: 40px;
      color: #fff;
      letter-spacing: -1px;
  
      &::before {
        display: block;
        content: "";
        width: 40px;
        height: 2px;
        margin-bottom: 24px;
        background-color: #fff;
      }
  
      @include forDesktop {
        font-size: 80px;
        line-height: 80px;
        letter-spacing: -1.5px;
      }
    }
  }