.button {
    font-family: inherit;
    display: flex;
    justify-content: center;
    align-items: center;
  
    box-sizing: border-box;
    width: 100%;
    height: 56px;
    border: 2px solid $perfectBlue;
    border-radius: 8px;
  
    text-decoration: none;
    color: $perfectBlue;
    font-weight: bold;
    font-size: 16px;
    line-height: 14px;
  
    transition: all 0.5s;
    cursor: pointer;
  
    &Primary {
      color: #fff;
      border-color: #005ecc;
      background-color: #005ecc;
    }
  
    &:hover {
      color: #fff;
      border-color: #005ecc;
      background-color: #005ecc;
      box-shadow: 0px 13px 27px rgba(0, 83, 181, 0.46);
    }
  
    &:active {
      border-color: #005ecc;
      background-color: #005ecc;
      box-shadow: 0px 8px 14px rgba(0, 83, 181, 0.46);
    }
  }