.grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20px;
  
    @include forDesktop() {
      grid-template-columns: repeat(12, 1fr);
      column-gap: 24px;
    }
  
    &Desktop {
      display: block;
  
      @include forDesktop() {
        display: grid;
      }
    }
  
    &_item {
      @for $start from 1 through 12 {
        @for $end from $start through 12 {
          &#{$start}-#{$end} {
            grid-column: #{$start} / #{$end + 1};
          }
  
          &Desktop#{$start}-#{$end} {
            @include forDesktop() {
              grid-column: #{$start} / #{$end + 1};
            }
          }
        }
      }
  
      
  
      &7-12 {
        grid-column: 7 / 13;
      }
    }
  }