.formField {
    display: block;
    box-sizing: border-box;
    width: 100%;
    height: 48px;
    padding: 14px 16px;
    border: 1px solid transparent;
    background-color: #f7f8ff;
    color: #000;
    border-radius: 8px;
  
    &::placeholder {
      font-size: 14px;
      line-height: 20px;
      color: #8f99a8;
    }
  
    &:hover {
      border-color: #ccd0e0;
    }
  
    &:focus {
      border-color: $perfectBlue;
      outline: none;
    }
  
    &Text {
      color: #8f99a8;
      height: 144px;
      font-family: inherit;
      resize: none;
    }
  }