.testimonial {
    &_photoContainer {
      display: grid;
      grid-template-columns: repeat (4, 1fr);
      column-gap: 24px;
  
      margin-bottom: 28px;
  
      @include forDesktop() {
        margin-bottom: 32px;
      }
    }
  
    &_photo {
      max-width: 350px;
      width: 100%;
      grid-column: 2 / 4;
      border-radius: 50%;
    }
  
    &_name {
      margin: 0 0 4px;
      font-weight: bold;
      font-size: 24px;
      line-height: 30px;
      text-align: center;
    }
  
    &_role {
      margin: 0 0 4px;
      font-size: 14px;
      line-height: 27px;
      text-align: center;
      color: #9fadc2;
    }
  
    &_about {
      text-align: center;
    }
  }