.place {
    &_photoContainer {
      position: relative;
      width: 100%;
      box-sizing: border-box;
      max-height: 256px;
      border-radius: 8px;
      overflow: hidden;
  
      &::after {
        display: block;
        content: "";
        padding-bottom: 100%;
      }
  
      @include forDesktop() {
        height: 256px;
        padding-bottom: 0;
      }
    }
  
    &_photo {
      position: absolute;
      height: 100%;
      width: 100%;
  
      object-fit: cover;
      object-position: center;
    }
  
    &_photos {
      margin-bottom: 16px;
  
      @include forDesktop() {
        margin-bottom: 24px;
      }
    }
  
    &_link {
      font-weight: bold;
      color: $perfectBlue;
      text-decoration: none;
    }
  }