.menu {
    background-color: $perfectBlue;
  
    &_content {
      box-sizing: border-box;
      padding: $headerTopPadding 0;
      height: 100vh;
    }
  
    &_top {
      margin-bottom: 48px;
    }
  
    &_list {
      margin: 0 0 48px;
      padding: 0;
      list-style: none;
    }
  
    &_item {
      margin-bottom: 24px;
  
      &:last-child {
        margin-bottom: 0;
      }
    }
  
    &_link {
      display: inline-block;
      padding-bottom: 8px;
      border-bottom: 1px solid transparent;
      text-decoration: none;
      color: #fff;
      font-size: 22px;
      line-height: 22px;
      letter-spacing: 2px;
      text-transform: uppercase;
      transition: border-color 0.5s;
  
      &:hover {
        border-color: #fff;
      }
    }
  
    &_call {
        display: block;
        width: max-content;
        text-decoration: none;
        font-weight: bold;
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 2px;
        text-transform: uppercase;
        color: #fff;
  
      &--phone {
        margin-bottom: 16px;
      }
  
      &--action {
        padding-bottom: 8px;
        border-bottom: 1px solid #fff;
      }
    }
  }